footer {
    background: var(--secondary-background-color);
    display: flex;
    align-items: center;
    gap: 0.5em;
    height: 12vh;
    justify-content: space-evenly;
}

.address-name,
.icon-map-marker {
    display: inline-block;
    /* align-items: center; */
    vertical-align: middle;
    justify-content: center;
}

/* .address-name {
  margin-left: 100px;
} */

.icon-map-marker {
    font-size: 34px;
    /* margin-left: 10px; */
}

.fa.fa-copyright,
p.copyright {
    display: inline-block;
    vertical-align: middle;
    /* 可选：垂直居中对齐 */
}


.about-us,
.social-media-icons {
    display: flex;
    align-items: center;
    flex-direction: column;
}

.about-us h3,
.about-us a {
    padding: 3px;
}


.social-media-icons {
    display: flex;
    flex-direction: column;
    align-items: center;
    padding:10px;
}

.social-media-icons a {
    padding: 10px;
    font-size: 24px;
}

.social-media-icons h3{
    margin-bottom: 10px;
}