.book-box {
    position:relative;
    width: calc(25% - 10px);
    text-align: center; /* 文本居中对齐 */
    display:flex;
    flex-direction:column;
    align-items:center;
    padding: 10px;
}

.book-image {
    position:relative;
}

.read-now-button {
    font-size: 10px;
    position:absolute;
    top:0;
    right:0;
    background-color:white;
    color:var(--primary-color);
    padding:5px 10px;
    border: none;
    border-radius: 50px;
}

.button:hover,
.button:active {
    background: var(--primary-color-dark);
    color: white;
}

.icon-read-now::before {
    content: "📖";
    margin-right: 5px;
    font-family: FontAwesome;
}

.add-to-cart-button {
    white-space: pre-line;
    background-color: #b40c0c;
    color:white;
    border-radius:50px;
    margin-top: 10px;
}

