.category-book-list-container {
    display:flex;
    align-items: center;
}

.category-book {
    width:100%;
}
.book-lists {
    width:100%;
    list-style-type: none; /* 去除列表样式 */
    display: flex; /* 使用 Flexbox 布局 */
    justify-content: space-between; /* 均匀分布各个书籍 */
    flex-wrap: wrap; /* 防止换行，确保水平排列 */
    padding: 0 50px; /* 去除默认内边距 */
}
